<template>
  <div class="uploader">
    <div class="heading">
      <h5>{{title}}
        <font color="#999">{{uploading}}</font>
      </h5>
    </div>
    <div v-show="!thumbnail" class="center">
      <div :id="parentInputId">
        <span id="pickfiles" />
        <div class="uploader__input-box">
          <label id="addShowLabel" @click="uploadFiles(inputId)">
            <button class="btn btn-success" @click.prevent="">Choose file</button>
          </label>
        </div>
      </div>
    </div>
    <div>
      <button
        v-show="thumbnail"
        @click="removeImage()"
        class="btn btn-danger"
      >
        Remove Image
      </button>
    </div>
    <div v-show="thumbnail" class="uploader_file">
      <img class="poster" :src="thumbnail" />
    </div>
    <div v-show="!thumbnail && poster !== 'none'" class="uploader_file">
      <img class="poster" :src="poster" />
    </div>
  </div>
</template>

<script>
  import {
    getToken
  } from 'src/helpers/tokenGenerator.js'

  export default {
    name: 'thumb',
    props: {
      title: String,
      uploadingText: String,
      poster: String,
      parentInputId: {
        default: 'thumbnail',
        type: String
      }
    },
    data: () => ({
      files: '',
      uploading: '',
      inputId: '',
      userInfo: '',
      thumbnail: ''
    }),
    created() {
      this.$nextTick(() => {
        this.uploadInit()
      })
    },
    methods: {
      removeImage() {
        this.thumbnail = null
        this.poster = null
        this.uploading = ''
        this.$emit('removeFile')
      },
      uploadFiles(id) {
        this.$el.querySelector(`#${id}`).click()
      },
      uploadInit() {
        this.upload()
        setTimeout(() => {
          this.inputId = this.$el.querySelector(`#${this.parentInputId} input`).getAttribute('id')
        }, 500)
      },
      upload() {
        const setting = {
          runtimes: 'html5',
          browse_button: 'pickfiles',
          uptoken: getToken(),
          unique_names: true,
          domain: 'https://images.wuhansocial.com',
          get_new_uptoken: false,
          container: this.parentInputId,
          max_file_size: '10mb',
          max_retries: 1,
          dragdrop: false,
          chunk_size: '4mb',
          auto_start: true,
          multi_selection: false,
          init: {
            FilesAdded: (up, files) => {
              this.uploading = this.uploadingText
            },
            FileUploaded: (up, file, info) => {
              const domain = up.getOption('domain')
              const { key } = JSON.parse(info)
              const sourceLink = `${domain}/${key}`
              this.thumbnail = sourceLink
              this.$emit('uploadPoster', sourceLink)
            },
            UploadComplete: () => {
              this.uploadInit()
              this.uploading = 'Uploaded'
            }
          }
        }
        Qiniu.uploader(setting) // eslint-disable-line
      }
    }
  }

</script>

<style scoped>
  .uploader {
    text-align: center;
  }

  .uploader__hd {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    padding-bottom: 10px;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .uploader__title {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
  }

  .uploader__info {
    color: #B2B2B2;
  }

  .uploader__bd {
    margin-bottom: -4px;
    margin-right: -9px;
    overflow: hidden;
  }

  .uploader_file {
    float: none;
    margin-right: 9px;
    margin-bottom: 9px;
    text-align: -webkit-center;
    margin-top: 20px;
  }

  .uploader__img {
    display: block;
    width: 79px;
    height: 79px;
    border-radius: 0;
  }

  .uploader__file_status {
    position: relative;
  }

  .uploader__file_status:before {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .uploader__file-content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    color: #FFFFFF;
  }

  .uploader__input-box {
    float: none!important;
    position: relative;
    margin-right: 9px;
    margin-bottom: 9px;
  }

  .uploader__input {
    float: none!important;
    position: relative;
    margin-right: 9px;
    margin-bottom: 9px;
    width: 100%;
    height: 100px;
    border: 1px solid #D9D9D9;
    border-radius: 0;
  }

  .remove {
    float: none;
    vertical-align: super;
  }

  .center {
    text-align: -webkit-center;
  }

  .poster {
    width: 50%;
  }

</style>



